import React from "react";
import {Link} from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faFortAwesomeAlt} from "@fortawesome/free-brands-svg-icons";


const Navigation = ({userObj}) => {

// return에서 지운 프로필 아이콘                     <FontAwesomeIcon icon={faUser} color={"#04AAFF"} size="2x" />

    return (
        <nav>
            <ul style={{ display: "flex", justifyContent: "center", marginTop: 50 }}>
                <li>
                    <Link to="/" style={{ marginRight: 10 }}>
                    <FontAwesomeIcon icon={faFortAwesomeAlt} color={"#98adfa"} size="3x" />
                    </Link>
                </li>
                <li>
                    <Link
                        to="/profile"
                        style={{
                            marginLeft: 10,
                            display: "flex",
                            flexDirection: "column",
                            alignItems: "center",
                            fontSize: 12,
                        }}
                    >
                    <img src={userObj.photoURL} className="profilePic" width="30px" height="30px" />
                        <span style={{ marginTop: 10 }}>
                            {userObj.displayName
                            ? `${userObj.displayName}의 Profile`
                            : "Profile"}
                        </span>
                    </Link>
                </li>
            </ul>
        </nav>
)};

export default Navigation;